/* Styles for all pages */
.App {
  font-family: "Robot", sans-serif;
  font-weight: 400;
  min-height: 100vh;

  background-color: #F7F7F7;
}

.Main{
  padding-top: 100px;
}

a:hover {
    text-decoration: none;
    color: black;
}

.Content {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 2px;
  border: 1px solid #dadadf;
}
.FillerImage {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
  
  .Image {
  max-height: 40vh;
  max-width: 100%;
  object-fit: contain;
}
  
* {
  box-sizing: border-box;
}
  
a {
  color: #555555;
  transition: color ease-in-out 0.3s;
}
  
a:hover {
  text-decoration: none;
}

a:hover i {
  color: #2142b2;
  transition: color ease-in-out 0.3s;
}

input[type="file"] {
  display: none;
}

i {
  color: #cfced3;
  font-size: 1.6rem;
  padding: 7px;
  vertical-align: middle;
}

i:hover {
  cursor: pointer;
  transition: color ease-in-out 0.3s;
  color: #2142b2;
}
figure {
  text-align: center;
  display: inline-block;
}
  
/* scrollbar */
*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: #f1f1f1;
}
*::-webkit-scrollbar-thumb {
  background: #888;
}
*::-webkit-scrollbar-thumb:hover {
  background: #555;
}
